// 
export function debounce(fn, delay) {
  let timer = null
  delay = delay || 200
  return function() {
    const args = arguments
    const that = this
    clearTimeout(timer) //
    timer = setTimeout(function() { //
      fn.apply(that, args)
    }, delay)
  }
}
// 
export function throttle(fn, delay) {
  let lastTime
  // let timer
  delay = delay || 1000
  return function() {
    const args = arguments
    // 
    const nowTime = Date.now()
    if (!lastTime || nowTime - lastTime > delay) {
      lastTime = nowTime //
      fn.apply(this, args)
    }
  }
} 
